<template>
  <div class="reportCenter">
    <div  v-loading="Statrloading" element-loading-text="拼命加载中...">
      <el-row :span="24">
        <!-- 渠道搜索 -->
        <el-col :span="2">
          <el-select v-model="qudaoInfo.valuequdao" filterable clearable allow-create default-first-option  placeholder="渠道" class='templeMaring' @focus="focusQudao" @change="valuequdaoChange">
          <el-option
            v-for="(item,index) in qudaoInfo.optionsqudao"
            :key="index"
            :label="item"
            :value="item">
          </el-option>
    </el-select>
        </el-col>
          <!-- 渠道号搜索 -->
        <el-col :span="2">
          <el-select  class='templeMaring' v-model="qudaoInfo.valuequdaonum"  filterable allow-create clearable default-first-option placeholder="渠道号" @focus="focusQudaonum" @change="valuequdaonumChange">
              <el-option
                v-for="(item,index) in qudaoInfo.optionsqudaonum"
                :key="index"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
         </el-col>
          <!-- 选择负责人 -->
         <el-col :span="2">
          <el-select  class='templeMaring'
              v-model="fuzherenname"   clearable
              placeholder="负责人">
              <el-option
                v-for="(item,index) in fuzheren"
                :key="index"
                :label="item.label"
                :value="item.value">
              </el-option>
           </el-select>
         </el-col>
         <!-- 选择渠道属性 -->
         <el-col :span="3">
          <el-select  class='templeMaring'
              v-model="qudaoshuxingname" clearable 
              @change="qudaoProprFilter"
              placeholder="选择渠道属性">
              <el-option
                v-for="(item,index) in qudaoshuxing"
                :key="index"
                :label="item.label"
                :value="item.value">
              </el-option>
          </el-select>
        </el-col>
        <!-- 选择渠道分类 -->
        <el-col :span="3">
          <el-select class='templeMaring' v-model="qudaoClass" clearable placeholder="渠道分类">
              <el-option-group
                v-for="(group,index) in options4"
                :key="index"
                :label="group.label">
                <el-option
                  v-for="item in group.options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-option-group>
            </el-select>
         </el-col>
          <!-- 选择APP默认乐贷款 -->
          <el-col :span="3">
              <el-select v-model="qudaoInfo.optionsApp" default-first-option  placeholder="APP" class='templeMaring'>
                <el-option
                  v-for="(item,index) in chooseApp"
                  :key="index"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
          </el-col>
          <!-- 分成方式 -->
          <el-col :span="3">
            <el-select v-model="rewardTypeValue" clearable placeholder="分成方式" class='templeMaring'>
              <el-option
                v-for="(item,index) in rewardTypeList"
                :key="index"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-col>
        <!-- 选择日期 -->
        <el-col :span="3">
         <div class="block dataStyle" >
            <el-date-picker
              v-model="gitTimeold"
              type="daterange"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              unlink-panels
              value-format="yyyy-MM-dd"
              @focus = "dataTime"
              >
            </el-date-picker>
         </div>
        </el-col>
      </el-row>
      <!-- 字段 -->
      <div class="divheight"></div>
      <el-row :span='24'>
        <el-col :span="1"><span class="fontchange">字段:</span></el-col> 
        <el-col :span="17" class="cheackTop">
          <el-checkbox-group v-model="checkList">
            <el-col :span="2">
               <el-checkbox label="总计" @change="fontchangeIsShow(1)"></el-checkbox>
            </el-col>
            <el-col :span="2">
               <el-checkbox label="渠道号" @change="fontchangeIsShow(2)"></el-checkbox>
            </el-col>
            <el-col :span="3">
               <el-checkbox label="渠道分类" @change="fontchangeIsShow(3)"></el-checkbox>
            </el-col>
            <el-col :span="2">
               <el-checkbox label="负责人" @change="fontchangeIsShow(4)"></el-checkbox>
            </el-col>
            <el-col :span="2">
                <el-checkbox label="H5" @change="fontchangeIsShow(5)"></el-checkbox>
            </el-col>
            <el-col :span="2">
               <el-checkbox label="额度" @change="fontchangeIsShow(6)"></el-checkbox>
            </el-col>
            <el-col :span="2">
               <el-checkbox label="首贷" @change="fontchangeIsShow(7)"></el-checkbox>
            </el-col>
            <el-col :span="2">
               <el-checkbox label="续贷" @change="fontchangeIsShow(8)"></el-checkbox>
            </el-col> 
            <el-col :span="2">
               <el-checkbox label="外部" @change="fontchangeIsShow(9)"></el-checkbox>
            </el-col> 
          </el-checkbox-group>
        </el-col> 
        <el-col :span="6" class="downsharchRight">
          <el-col :span="8" >
             <el-button type="info" icon="el-icon-search" size='mini' @click='search' class="searchStyle"></el-button>
          </el-col>
          <el-col :span="8" >
             <el-button type="success"  :class="iconImg"  size='mini' @click='tableTimeStyle'></el-button>
          </el-col>
          <el-col :span="8" >
             <el-button type="primary" class="fa fa-cloud-download" size='mini' @click="downData"></el-button>
          </el-col>
        </el-col>
      </el-row>
  </div>
    <!-- 表格 -->
    <div class="table-wrapper tablepadding" v-loading="Statrloading" element-loading-text="拼命加载中...">
      <el-table :data="fbsData"  :key="key" style="width: 100%" :height="fbsData&&fbsData.length>0?fbsDataHeight:150" border  size="mini" v-loading="tableloading" element-loading-text="拼命加载中..." @row-click="clickRow" :highlight-current-row="true">
         <el-table-column prop="date" :label="tableHeader.headerTit"  :key="tableHeader.headerTit" :width="tableHeader.headerTit=='日期'?160:'auto'"  v-for="(tableHeader,key) in headerTitle" v-if="tableHeader.showStatus" align="center">
            <template slot-scope="scope">
                {{scope.row[key]}}{{tableHeader.headerTit|addSuffix}}
            </template>
         </el-table-column>
                 
      </el-table>
      <el-pagination
          background
          @size-change="pageSizeChange"
          @current-change="currentPageChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="dataSize" class="paginationWrapper">
      </el-pagination>
    </div>
   </div>
</template>

<script>
import {
  getChannelParameterReg,
  getChannelAttribute,
  getReportform,
  downloadDataReg
} from "../../api/api.js";
export default {
  data() {
    return {
      // 用户sessionid
      sessionid: "",
      Statrloading: true,
      // 渠道分类市场BD和子信息保存便于根据选择属性显示不同信息
      channelType: [],
      channelSubdivision: [],
      // 获取当前时间
      newDate: "",
      // 渠道
      qudaoInfo: {
        // 渠道值
        valuequdao: "",
        // 渠道信息，渠道号信息
        optionsqudao: [],
        optionsqudaonum: [],
        // 渠道号值
        valuequdaonum: "",
        //APP值
        optionsApp: 1
      },
      // 选择负责人
      fuzheren: [],
      fuzherenname: "",
      // 选择渠道属性
      qudaoshuxing: [
        {
          value: "-1",
          label: "全部"
        }
      ],
      qudaoshuxingname: "",
      // 选择渠道分类
      options4: [],
      qudaoClass: "",
      // App选择
      chooseApp: [],
      // 分成方式
      rewardTypeValue: "", //分成方式选择的值
      rewardTypeList: [], //分成方式信息
      // 版本
      versionsData: [
        {
          value: "-1",
          label: "全部"
        }
      ],
      versions: "",
      // 日期
      gitTimeold: [],
      gitTimeNew: {},
      // 字段
      checkList: ["总计","渠道号","渠道分类","负责人","H5","额度","首贷","续贷","外部"],
      // 表格显示时间样式
      // iconImg:'fa fa-times-circle',
      iconImg: "fa fa-sun-o",
      // 命名一个变量来判断icon是第几个
      iconImgIndex: 1,
      // 搜索获取的数据
      fbsData: [],
      // 表格高度
      fbsDataHeight: null,
      tableloading: false,
      // 记录字段勾选时候搜索显示隐藏
      fontCheack: {
        total: false,
        qudaoNumber: true,
        qudaoClass: true,
        fuzeren: true,
        H5: true,
        moneySize: true,
        fristHaveMoney: true,
        renew: true,
        without: true,
        time: true,
        version: true
      },
      key:1,//table key
      // 表头
      headerTitle: [],
      // 搜索发送的数据
      secrchSetData: {},
      // 分页信息
      currentPage: 1,
      pageSizes: [50, 100,300,500],
      dataSize: 0,
      pagesize: 50,
      first: 0,
      // 日 月 条数
      dataTimeDay: 0,
      dataTimeMoth: 0,
      baobiaoSearchIndex: 0,
      downDataInfo: {}
    };
  },
  watch: {
      checkList(valArr) {
        this.key = this.key + 1// 为了保证table 每次都会重渲
      }
  },
  filters: {
    addSuffix(value) {
      if (value == "H5激活转化" ||value == "注册转化" ||value == "进件转化" ||value == "开户转化" ||value == "提现转化" ||value == "毛利率" ||value == "优化比例") {
        return "%";
      } else {
        return;
      }
    }
  },
  methods: {
    getClientHeight() {
      let clientHeight = window.document.documentElement.clientHeight;
      this.fbsDataHeight = clientHeight - 260;
    },
    // 页面加载时获取搜索框中要显示的内容
    sharchInfoSelect(sessionid) {
      var statusqudaoInfo = false;
      var statusclass = false;
      var _this = this;
      var data = {
        sessionid: sessionid
      };
      var dataStr = JSON.stringify(data);
      // 获取渠道渠道号接口,获取负责人信息接口数据
      getChannelParameterReg(dataStr).then(res => {
        console.log("获取报表搜索的数据");
        console.log(res.data);
        if (res.data.status === 0) {
          for (var i = 0; i < res.data.adminIdList.length; i++) {
            let id = res.data.adminIdList[i];
            let name = res.data.adminIdList[i].split(",")[1];
            _this.fuzheren.push({ value: id, label: name });
            _this.qudaoInfo.optionsqudao = res.data.channelNameList;
            _this.qudaoInfo.optionsqudaonum = res.data.channelNoList;
            statusqudaoInfo = true;
          }
        }
        if (res.data.status === 1) {
          _this.$router.replace("/login");
        }
      });
      // 获取渠道属性属性分类信息
      getChannelAttribute(dataStr).then(res => {
        console.log("获取渠道分类搜索信息");
        console.log(res.data);
        // 分成方式赋值
        _this.rewardTypeList = res.data.rewardTypeList;
        // app搜索选择的数据
        _this.chooseApp = res.data.appList;
        if (res.data.status === 0) {
          for (var j = 0; j < res.data.channelAttribute.length; j++) {
            // let id = res.data.channelAttribute[j].higherId+','+res.data.channelAttribute[j].id;
            let id = res.data.channelAttribute[j].id;
            let name = res.data.channelAttribute[j].name;
            _this.qudaoshuxing.push({ value: id, label: name });
          }
          _this.channelType = res.data.channelType;
          _this.channelSubdivision = res.data.channelSubdivision;
          statusclass = true;
        }
        if (res.data.status === 1) {
          _this.$router.replace("/login");
        }
      });
      var trime = setInterval(function() {
        _this.baobiaoSearchIndex++;
        if (statusqudaoInfo && statusclass) {
          _this.Statrloading = false;
          clearInterval(trime);
          _this.baobiaoSearchIndex = 0;
          _this.search();
        } else {
          if (_this.baobiaoSearchIndex < 2) {
            _this.sharchInfoSelect(data.sessionid);
          } else {
            _this.$message.error("数据获取超时");
            clearInterval(trime);
            _this.baobiaoSearchIndex = 4;
            _this.Statrloading = false;
          }
        }
      }, 2000);
    },
    // 渠道属性过滤根据选择显示渠道分类
    qudaoProprFilter(val) {
      // var val = parseInt(val.split(",")[1]);
      this.qudaoClass = "";
      this.options4 = [{ options: [{ value: "-1,-1", label: "全部" }] }];
      // this.options4 = [{options:[{value: '-1',label: '全部'}]}];
      for (var i = 0; i < this.channelType.length; i++) {
        if (val === this.channelType[i].higherId) {
          let id = this.channelType[i].id;
          let label = this.channelType[i].name;
          this.options4.push({ label: label, id: id });
        }
        for (var j = 1; j < this.options4.length; j++) {
          this.options4[j].options = [];
          for (var m = 0; m < this.channelSubdivision.length; m++) {
            if (this.options4[j].id === this.channelSubdivision[m].higherId) {
              this.options4[j].options.push({
                value:
                  this.channelSubdivision[m].higherId +
                  "," +
                  this.channelSubdivision[m].id,
                label: this.channelSubdivision[m].name
              });
              // this.options4[j].options.push({value:this.channelSubdivision[m].id,label:this.channelSubdivision[m].name})
            }
          }
        }
      }
    },
    findTime() {
      var myDate = new Date();
      //获取当前年
      var year = myDate.getFullYear();
      //获取当前月
      var month = myDate.getMonth() + 1;
      //获取当前日
      var date = myDate.getDate();
      this.newDate = year + "-" + month + "-" + date;
    },
    tableTimeStyle() {
      this.iconImgIndex++;
      if (this.iconImgIndex <= 3) {
        if (this.iconImgIndex === 1) {
          this.iconImg = "fa fa-sun-o";
        }
        if (this.iconImgIndex === 2) {
          this.iconImg = "fa fa-moon-o";
        }
        if (this.iconImgIndex === 3) {
          this.iconImg = " el-icon-circle-close";
        }
      } else {
        this.iconImgIndex = 1;
        this.iconImg = "fa fa-sun-o";
      }
      this.currentPage = 1;
      this.first = 0;
      this.search();
    },
    // 选择页发生改变
    pageSizeChange(res) {
      this.pagesize = res;
      this.first = 1;
      this.search();
    },
    // 当前页发生改变
    currentPageChange(res) {
      this.currentPage = res;
      this.first = 1;
      this.search();
    },
    // 搜索事件
    search() {
      // 进行搜索时候勾选字段全部勾选
      this.checkList = ["总计", "渠道号","渠道分类","负责人","H5","额度","首贷","续贷","外部"];
      this.headerTitle = [];
      this.fbsData = [];
      this.tableloading = true;
      // 判断是否选择了时间区间
      if (this.gitTimeold) {
        this.gitTimeNew.satrt = this.gitTimeold[0];
        this.gitTimeNew.end = this.gitTimeold[1];
      } else {
        this.gitTimeNew.satrt = "";
        this.gitTimeNew.end = "";
      }
      // 字段发送值
      this.secrchSetData = {
        sessionid: this.sessionid,
        channelName: this.qudaoInfo.valuequdao ? this.qudaoInfo.valuequdao : "",
        channel: this.qudaoInfo.valuequdaonum? this.qudaoInfo.valuequdaonum: "",
        appId: this.qudaoInfo.optionsApp,
        adminId: this.fuzherenname ? this.fuzherenname.split(",")[0] : "",
        adminName: this.fuzherenname ? this.fuzherenname.split(",")[1] : "",
        attribute:this.qudaoshuxingname && this.qudaoshuxingname != "-1"? this.qudaoshuxingname: "",
        type:this.qudaoClass && this.qudaoClass != "-1,-1"? this.qudaoClass.split(",")[0]: "",
        subdivision:this.qudaoClass && this.qudaoClass != "-1,-1"? this.qudaoClass.split(",")[1]: "",
        // 选择小时，版本
        changHouser: this.changHouser,
        // 分成方式
        rewardTypeId: this.rewardTypeValue,
        // 选择查询时间
        minDate: this.gitTimeNew.satrt,
        maxDate: this.gitTimeNew.end,
        dateType: this.iconImgIndex ? this.iconImgIndex : "",
        pageId: this.currentPage - 1,
        pageSize: this.pagesize,
        first: this.first,
        tag: this.checkList,
        seo: 0
      };
      var _this = this;
      var searchSetData = JSON.stringify(this.secrchSetData);
      // 搜索方式请求
      console.log(searchSetData);
      getReportform(searchSetData).then(res => {
          console.log(res.data);
          if (res.data.status === 0) {
            // 进行搜索时候表头和搜索值先进行初始化
            _this.headerTitle = [];
            _this.fbsData = [];
            // 获取搜索值
            _this.fbsData = res.data.datas;
            _this.fbsData.map((value,index)=>{
              return isNaN(value)? value : parseFloat(value)

            })
            // 获取表头名字
            let headerName = res.data.proxyNameList;
            // 处理表头增加一个对象用于勾线显示隐藏
            headerName.map((value, index) => {
              _this.headerTitle.push({ headerTit: value, showStatus: true });
            });
            ///
            if (!res.data.listSize) {
              _this.dataSize = _this.dataSize;
            } else {
              if (this.secrchSetData.dateType === 1) {
                _this.dataTimeDay = parseInt(res.data.listSize);
                _this.dataSize = _this.dataTimeDay;
              }
              if (this.secrchSetData.dateType === 2) {
                _this.dataTimeMoth = parseInt(res.data.listSize);
                _this.dataSize = _this.dataTimeMoth;
              }
              if (this.secrchSetData.dateType === 3) {
                _this.dataTimeMoth = parseInt(res.data.listSize);
                _this.dataSize = _this.dataTimeMoth;
              }
              // _this.dataSize = parseInt(res.data.listSize);
            }
            setTimeout(() => {
              _this.tableloading = false;
            }, 1000);
            _this.first = 0;
          }
          if (res.data.status === 1) {
            _this.$router.replace("/login");
          }
        })
        .catch(res => {
          _this.$message.error("数据搜索失败");
          _this.tableloading = false;
        });
    },
    focusQudao() {
      this.qudaoInfo.valuequdao = "";
    },
    focusQudaonum() {
      this.qudaoInfo.valuequdaonum = "";
    },
    valuequdaoChange() {
      this.qudaoInfo.valuequdao = this.qudaoInfo.valuequdao.replace(/\-/gi, "");
    },
    valuequdaonumChange() {
      // this.qudaoInfo.valuequdaonum = this.qudaoInfo.valuequdaonum.replace(/\-/ig,'');
      this.qudaoInfo.valuequdaonum = this.qudaoInfo.valuequdaonum;
    },
    // 字段点击显示表格信息
    fontchangeIsShow(num) {
      console.log(num);
      switch (num) {
        case 1:
          this.fontCheack.total = !this.fontCheack.total;
          break;
        case 2:
          this.fontCheack.qudaoNumber = !this.fontCheack.qudaoNumber;
          this.headerTitle.map((value, index) => {
            if ("渠道" == value.headerTit || "渠道号" == value.headerTit) {
              this.$set(this.headerTitle, index, {
                headerTit: value.headerTit,
                showStatus: this.fontCheack.qudaoNumber
              });
            }
          });
          break;
        case 3:
          this.fontCheack.qudaoClass = !this.fontCheack.qudaoClass;
          this.headerTitle.map((value, index) => {
            if ("渠道分类" == value.headerTit ||"分成方式" == value.headerTit) {
              this.$set(this.headerTitle, index, {
                headerTit: value.headerTit,
                showStatus: this.fontCheack.qudaoClass
              });
            }
          });
          break;
        case 4:
          this.fontCheack.fuzeren = !this.fontCheack.fuzeren;
          this.headerTitle.map((value, index) => {
            if ("负责人" == value.headerTit) {
              this.$set(this.headerTitle, index, {
                headerTit: value.headerTit,
                showStatus: this.fontCheack.fuzeren
              });
            }
          });
          break;
        case 5:
          this.fontCheack.H5 = !this.fontCheack.H5;
          this.headerTitle.map((value, index) => {
            if ("H5点击" == value.headerTit || "H5注册" == value.headerTit) {
              this.$set(this.headerTitle, index, {
                headerTit: value.headerTit,
                showStatus: this.fontCheack.H5
              });
            }
          });
          break;
        case 6:
          this.fontCheack.moneySize = !this.fontCheack.moneySize;
          this.headerTitle.map((value, index) => {
            if ( "授信总额" == value.headerTit || "人均批额" == value.headerTit) {
              this.$set(this.headerTitle, index, {
                headerTit: value.headerTit,
                showStatus: this.fontCheack.moneySize
              });
            }
          });
          break;
        case 7:
          this.fontCheack.fristHaveMoney = !this.fontCheack.fristHaveMoney;
          this.headerTitle.map((value, index) => {
            if ("首提人数" == value.headerTit ||"首贷金额" == value.headerTit ||"外部首贷金额" == value.headerTit ||"首贷笔均" == value.headerTit) {
              this.$set(this.headerTitle, index, {
                headerTit: value.headerTit,
                showStatus: this.fontCheack.fristHaveMoney
              });
            }
          });
          break;
        case 8:
          this.fontCheack.renew = !this.fontCheack.renew;
          this.headerTitle.map((value, index) => {
            if ( "续贷人数" == value.headerTit || "续放笔数" == value.headerTit || "续贷金额" == value.headerTit || "续贷笔均" == value.headerTit ) {
              this.$set(this.headerTitle, index, {
                headerTit: value.headerTit,
                showStatus: this.fontCheack.renew
              });
            }
          });
          break;
        case 9:
          this.fontCheack.without = !this.fontCheack.without;
          this.headerTitle.map((value, index) => {
            if ("外部注册" == value.headerTit ||"外部进件" == value.headerTit ||"外部开户" == value.headerTit ||"外部首提" == value.headerTit ||"外部渠道提现金额" == value.headerTit) {
              this.$set(this.headerTitle, index, {
                headerTit: value.headerTit,
                showStatus: this.fontCheack.without
              });
            }
          });
          break;
      }
    },
    // 默认时间昨天和今天当用户选择时候执行事件清空默认值
    dataTime() {
      this.gitTimeold = [];
    },
    // yyyy-mm-dd
    dataTimeYMD(day) {
      var s1year = day.getFullYear();
      var s1month =
        day.getMonth() + 1 < 9
          ? "0" + (day.getMonth() + 1)
          : day.getMonth() + 1;
      var s1day = day.getDate() < 9 ? "0" + day.getDate() : day.getDate();
      var s1 = s1year + "-" + s1month + "-" + s1day;
      return s1;
    },
    clickRow(row, event, clickRow) {
      console.log(row);
      console.log(event);
      console.log(clickRow);
    },
    // 给表格增加序号
    indexFormatter1(index) {
      var newIndex;
      newIndex = this.pagesize * (this.currentPage - 1) + 1 + index;
      return newIndex;
    },
    // 下载事件
    downData() {
      // console.log('--------------------')
      this.downDataInfo = this.secrchSetData;
      console.log(this.downDataInfo);
      let downDataInfo = JSON.stringify(this.downDataInfo);
      console.log(downDataInfo);
      downloadDataReg(downDataInfo).then(res => {
        console.log(res.data);
        const content = res.data;
        const blob = new Blob([content]);
        const fileName = "运营数据详情统计表.xls";
        if ("download" in document.createElement("a")) {
          // 非IE下载
          const elink = document.createElement("a");
          elink.download = fileName;
          elink.style.display = "none";
          elink.href = URL.createObjectURL(blob);
          document.body.appendChild(elink);
          elink.click();
          URL.revokeObjectURL(elink.href); // 释放URL 对象
          document.body.removeChild(elink);
        } else {
          // IE10+下载
          navigator.msSaveBlob(blob, fileName);
        }
      });
    }
  },
  activated() {
      // 获取用户sessionid
      var userperadmin = localStorage.getItem("userperadmin");
      this.sessionid = JSON.parse(userperadmin).sessionid;
      //昨天的时间
      var day1 = new Date();
      day1.setTime(day1.getTime() - 24 * 60 * 60 * 1000);
      var s1 =
        day1.getFullYear() +
        "-" +
        (day1.getMonth() + 1 >= 10
          ? day1.getMonth() + 1
          : "0" + (day1.getMonth() + 1)) +
        "-" +
        (day1.getDate() >= 10 ? day1.getDate() : "0" + day1.getDate());
      //今天的时间
      var day2 = new Date();
      day2.setTime(day2.getTime());
      var s2 =
        day2.getFullYear() +
        "-" +
        (day2.getMonth() + 1 >= 10
          ? day2.getMonth() + 1
          : "0" + (day2.getMonth() + 1)) +
        "-" +
        (day2.getDate() >= 10 ? day2.getDate() : "0" + day2.getDate());
      this.gitTimeold = [s1, s2];
      (this.fontCheack = {
        total: false,
        qudaoNumber: true,
        qudaoClass: true,
        fuzeren: true,
        H5: true,
        moneySize: true,
        fristHaveMoney: true,
        renew: true,
        without: true,
        time: true,
        version: true
      }),
        // 使日历对应当前时间日期
        this.findTime();
      //获取屏幕高度
      this.getClientHeight();
      // 获取搜索选择的信息负责人和选择渠道属性选择渠道分类以及渠道和渠道号
      this.sharchInfoSelect(this.sessionid);
  }
};
</script>
<style scoped lang="scss">
.templeMaring {
  margin-right: 1rem;
}
.fontchange {
  font-size: 0.7rem;
}
.cheackTop {
  margin-top: 0.2rem;
}
.fa {
  font-size: 0.6rem;
}
.table-wrapper {
  margin-top: 2rem;
}
.divheight {
  height: 1rem;
}
</style>


